.banner{
 
    background-size: cover;
    height: 448px;
    color: white;
}
.content{
    padding-top: 140px;
    height: 190px;
    margin-left: 2rem;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: rgb(255, 255, 255);
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: rgb(255, 255, 255);
    background-color: #910000;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    height: 80px;
    max-width: 360px;
}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent , rgba(37,37,37,.6),#111);
}
